.team {
  &-background {
    background-color: #f7f7f7;
  }
  &-container {
    @extend %container;
    @media screen and (max-width: $mobile-max) {
      padding-top: 84px;
      padding-bottom: 54px;
    }
    @media screen and (min-width: $tablet-min) and (max-width: $tablet-max) {
      padding: 100px 159px;
    }
    @media screen and (min-width: $desktop) {
      padding-top: 130px;
      padding-bottom: 100px;
    }
  }
  &-subtitle {
    @extend %subtitle;
    color: $accent-color;
    padding-bottom: 20px;
  }
  &-title {
    @extend %h;
    color: $accent-color;
    padding-bottom: 44px;
  }
  &-list {
    @media screen and (min-width: $tablet-min) {
      display: flex;
      flex-wrap: wrap;
    }
  }

  // ! ITEM mobile & tablet
  &-item:not(:last-child) {
    @media screen and (max-width: $tablet-max) {
      margin-bottom: 64px;
    }
  }

  // ! ITEM desctop
  &-item:not(:nth-child(3n)) {
    @media screen and (min-width: $desktop) {
      margin-right: 30px;
    }
  }
  &-item {
    padding-bottom: 30px;
    @media screen and (min-width: $desktop) {
      flex-basis: calc((100% - 2 * 30px) / 3);
    }
  }
  &-title-h3 {
    @extend %h;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.38;
    letter-spacing: 0.02em;
    text-align: center;
    color: $accent-color;
  }

  &-description {
    font-weight: 400;
    font-size: 14px;
    height: 50px;
    line-height: 1.35;
    letter-spacing: 0.02em;
    text-align: center;
    color: $accent-color;
    margin-top: 8px;
    margin-bottom: 30px;
  }
  &-name-description-container {
    padding-top: 10px;
    padding-left: 10px;
    padding-right: 10px;

    background: linear-gradient(180deg, #090b13 0%, rgba(9, 11, 19, 0.2) 100%);
  }
}
